import { useEffect, useState } from "react";
import { Link as ScrollLink, Events, scrollSpy } from "react-scroll";
import { FaEnvelope, FaPhone } from "react-icons/fa";

export default function Header() {
  const [activeSection, setActiveSection] = useState("");

  useEffect(() => {
    Events.scrollEvent.register("begin", () => {});
    Events.scrollEvent.register("end", () => {});
    scrollSpy.update();

    return () => {
      Events.scrollEvent.remove("begin");
      Events.scrollEvent.remove("end");
    };
  }, []);

  const navItems = [
    { id: "about", label: "O nás" },
    { id: "partners", label: "Oficiálne zastúpenia" },
    { id: "contact", label: "Kontakt" },
  ];

  return (
    <header
      className="fixed top-0 left-0 w-full bg-white shadow-md z-50"
      style={{ padding: "12px 32px", display: "flex", justifyContent: "space-between", alignItems: "center" }}
    >
      <div className="text-xl font-bold">ELMORA</div>

      <nav className="flex gap-6 items-center">
        {navItems.map((item) => (
          <ScrollLink
            key={item.id}
            to={item.id}
            spy={true}
            smooth={true}
            duration={500}
            offset={-70}
            className={`cursor-pointer font-medium transition px-2 py-1 rounded ${
              activeSection === item.id ? "bg-green-700 text-white" : "hover:text-green-800"
            }`}
            onSetActive={() => setActiveSection(item.id)}
          >
            {item.label}
          </ScrollLink>
        ))}

        <a
          href="mailto:info@elmoraslovakia.sk"
          className="text-gray-600 hover:text-green-700"
          title="Napísať e-mail"
        >
          <FaEnvelope size={18} />
        </a>

        <a
          href="tel:+421912345678"
          className="text-gray-600 hover:text-green-700"
          title="Zavolať"
        >
          <FaPhone size={18} />
        </a>
      </nav>
    </header>
  );
}
