import { useEffect, useState } from "react";
import { Element } from "react-scroll";
import Header from "../components/Header";
import { FaEnvelope, FaPhone } from "react-icons/fa";

export default function HomePage() {
  const [activeSection, setActiveSection] = useState("about");

  useEffect(() => {
    const handleScroll = () => {
      const sections = ["about", "partners", "contact"];
      let current = "about";

      sections.forEach((id) => {
        const section = document.getElementById(id);
        if (section && section.getBoundingClientRect().top <= 100) {
          current = id;
        }
      });

      setActiveSection(current);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div
      className="bg-[url('/full_bg.png')] bg-no-repeat bg-top bg-cover text-gray-900"
      style={{ minHeight: "100vh" }}
    >
      <Header activeSection={activeSection} />

      {/* Hero Sekcia */}
      <section className="flex flex-col justify-center items-center text-center h-screen px-6">
        <h1 className="text-4xl md:text-5xl font-bold mb-4">
          Vitajte na stránke <span className="text-green-600">ELMORA</span>
        </h1>
        <p className="text-lg text-gray-700 max-w-2xl">
          Moderné riešenia pre náročné odvetvia. Kvalita, spoľahlivosť a inovácia.
        </p>
        <a
          href="#about"
          className="mt-6 inline-block bg-green-600 text-white px-6 py-2 rounded-full shadow hover:bg-green-700 transition"
        >
          Zistiť viac
        </a>

        <div className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-6 w-full max-w-6xl">
          <div className="border border-green-600 rounded-lg p-6 text-center">
            <h3 className="text-green-700 font-semibold mb-2">Kvalita bez kompromisov</h3>
            <p className="text-sm text-gray-700">
              Pracujeme výhradne s certifikovanými materiálmi a overenými partnermi, aby sme zabezpečili maximálnu spokojnosť.
            </p>
          </div>
          <div className="border border-green-600 rounded-lg p-6 text-center">
            <h3 className="text-green-700 font-semibold mb-2">Spoľahlivý partner</h3>
            <p className="text-sm text-gray-700">
              Našich klientov sprevádzame každým krokom — od konzultácie až po realizáciu dodávok a podporu.
            </p>
          </div>
          <div className="border border-green-600 rounded-lg p-6 text-center">
            <h3 className="text-green-700 font-semibold mb-2">Technické inovácie</h3>
            <p className="text-sm text-gray-700">
              Sledujeme aktuálne trendy a prinášame moderné riešenia šité na mieru rôznym segmentom trhu.
            </p>
          </div>
        </div>
      </section>

      {/* O nás */}
      <Element name="about">
        <section id="about" className="px-6 py-24 text-center bg-white">
          <h2 className="text-3xl font-semibold mb-6">O nás</h2>
          <p className="text-gray-700 text-lg max-w-3xl mx-auto">
            Spoločnosť ELMORA vznikla s cieľom priniesť kvalitu, odbornosť a spoľahlivosť do každej oblasti, v ktorej pôsobíme. Naša vízia je poskytovať špičkové služby a produkty, ktoré klientom uľahčia život.
          </p>
        </section>
      </Element>

      {/* Oficiálne zastúpenia */}
      <Element name="partners">
        <section id="partners" className="px-6 py-20 bg-gray-50 text-center">
          <h2 className="text-3xl font-semibold mb-10">Oficiálne zastúpenia</h2>
          <p className="text-gray-600 text-lg">
            Tu budú zobrazené logá alebo popisy partnerov/zastúpení. (Doplň neskôr podľa potreby)
          </p>
        </section>
      </Element>

      {/* Kontakt */}
      <Element name="contact">
        <section id="contact" className="px-6 py-20 bg-gray-100 text-center">
          <h2 className="text-3xl font-semibold mb-6">Kontakt</h2>
          <div className="text-lg text-gray-700 space-y-4">
            <div>
              <FaEnvelope className="inline-block mr-2 text-green-600" />
              <a href="mailto:info@elmoraslovakia.sk" className="text-green-600 underline">
                info@elmoraslovakia.sk
              </a>
            </div>
            <div>
              <FaPhone className="inline-block mr-2 text-green-600" />
              <a href="tel:+421912345678" className="text-green-600 font-medium">
                +421 912 345 678
              </a>
            </div>
          </div>
        </section>
      </Element>

      {/* Footer */}
      <footer className="px-6 py-10 text-center text-sm text-gray-500 bg-white">
        © {new Date().getFullYear()} ELMORA. Všetky práva vyhradené.
      </footer>
    </div>
  );
}
